* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;

.fourthPage {
    height: 100vh;
    width: 100%;
    background-color: black;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;

    .containerOne {
        position: relative;

        .containerInsideOne {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);


            img {
                height: 52px;
                display: block;
                margin: 0 auto;
            }

            h1 {
                text-align: center;
                color: $main-color;
                font-size: 0.8rem;
                padding: 5%;
                font-family: 'Bitter', serif;
                font-weight: 400;
                letter-spacing: 1px;
            }

            p {
                color: whitesmoke;
                text-align: center;
                font-size: 0.6rem;
                font-family: 'Bitter', serif;
                font-weight: 400;
            }
        }
    }

    .containerTwo {
        position: relative;

        .containerInsideTwo {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            img {
                height: 52px;
                display: block;
                margin: 0 auto;
            }
    
            h1 {
                text-align: center;
                color: $main-color;
                font-size: 0.8rem;
                padding: 5%;
                font-family: 'Bitter', serif;
                font-weight: 400;
                letter-spacing: 1px;
            }
    
            p {
                color: whitesmoke;
                text-align: center;
                font-size: 0.6rem;
                font-family: 'Bitter', serif;
                font-weight: 400;
            }
        }

        .arrowDownThree {
            height: 26px;
            position: absolute;
            right: 5%;
            bottom: 0 !important;
            transform: translate(-50%, -50%);
            cursor: pointer;
        }
    }
}


